const getHtmlAttributes = (props) => {
    const keys = Object.keys(props);
    const filtered = {};
    for (let index = keys.length - 1; index >= 0; --index) {
        const key = keys[index];
        if (!key.startsWith('$')) {
            filtered[key] = props[key];
        }
    }
    return filtered;
};
export { getHtmlAttributes };
