const createServerStylesheet = () => {
    let cssString = '';
    let data = '';
    const stylesheet = {
        get cssString() {
            return cssString;
        },
        get data() {
            return data;
        },
        update: (newCssString, newData) => {
            cssString = newCssString;
            data = newData;
            return stylesheet;
        },
    };
    return stylesheet;
};
export { createServerStylesheet };
